'use strict';

import $ from 'jquery';

export default class Tabs {
	constructor() {
		let $tabsWrapper = $('.tab-wrapper'),
			$tabs = $('.tab-wrapper__tabs', $tabsWrapper),
			$content = $('.tab-wrapper__contents', $tabsWrapper);

		$('> *:not(.active)', $content).fadeOut();
		$('.active', $content).fadeIn();

		$('a.tab-controller', $tabs).map((i,ele) => {
			let $this = $(ele);

			$this.on('click', (e) => {
				e.preventDefault();

				let target = $this.attr('href');

				this.switchTabs($this, target, $content);
			})
		})
	}

	switchTabs($object, target, $content) {
		if(!$object.parent().hasClass('active')){
			//button handler
			$('.active', $('.tab-wrapper__tabs')).removeClass('active');
			$object.parent().addClass('active');
			
			// fade in fade out content
			$('.active', $content).fadeOut(300);
			$('.active', $content).removeClass('active');
			setTimeout(function() {
				$(target).fadeIn();
				$(target).addClass('active');
				if ($(target).find('.tabbed-carousel').length){
					$('.tabbed-carousel__items', $(target)).slick('setPosition');
				}
			}, 300);
		}
		else {
			return;
		}
	}
}
