    /**
     * Ajax call to endpoint
     */
    getResult(endpoint, cb, data) {
        let postData = {};

        $.extend(postData, data);

        $.ajax({
            url: endpoint,
            data: postData,
            dataType: 'json',
            cache: false
        }).done(cb);
    }
